@import url(https://fonts.googleapis.com/css2?family=EB+Garamond:ital,wght@1,700&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
:root {
  --background-color: rgb(153, 91, 153);
  --background-color-secondary: purple;
  --primary-color: rgb(8, 8, 8);
  --primary-color-secondary: rgb(240, 184, 184);
  --dark: rgb(173, 7, 46);
  --light: rgb(240, 197, 207);

  --container--large: 75%;
  --container-medium: 86%;
  --container-small: 90;
  --gradient: linear-gradient(
    to right top,
    #e986a8,
    #d36a96,
    #bc4f86,
    #a43277,
    #8c0c69
  );
}
html {
  scroll-behavior: smooth;
}
body {
  font-family: "EB Garamond", serif;
  background-color: rgb(153, 91, 153);
  background-color: var(--background-color);
  color: rgb(240, 197, 207);
  color: var(--light);
  /* background-image: url("./assest/programmer-bg.jpg");
  background-repeat: none;
  background-position: auto;
  background-size: 95%;
  background-clip: border-box; */
}
.container {
  width: 75%;
  width: var(--container--large);
  margin: 9.5%;
  text-align: left;
}
.container h1,
h2,
h3 {
  text-align: left;
}
h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}
h1 {
  font-size: 2.5rem;
}
section > h2,
section > h5,
h1 {
  text-align: center;
  color: rgb(240, 197, 207);
  color: var(--light);
}
section > h2 {
  color: rgb(8, 8, 8);
  color: var(--primary-color);
}
.text-light {
  color: rgb(240, 197, 207);
  color: var(--light);
}
a {
  color: rgb(240, 197, 207);
  color: var(--light);
  transition: all 400ms ease;
}

a:hover {
  color: rgb(240, 197, 207);
  color: var(--light);
}
.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: rgb(8, 8, 8);
  color: var(--primary-color);
  background: rgb(240, 197, 207);
  background: var(--light);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid rgb(8, 8, 8);
  border: 1px solid var(--primary-color);
  transition: all 400ms ease;
}

.btn:hover {
  background: rgb(240, 197, 207);
  background: var(--light);
  color: rgb(153, 91, 153);
  color: var(--background-color);
  border-color: transparent;
  box-shadow: 2%;
}

.btn-primary {
  background-color: rgb(8, 8, 8);
  background-color: var(--primary-color);
  color: rgb(153, 91, 153);
  color: var(--background-color);
}
img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/*Tablet Media Query*/

@media screen and(max-width:1024px) {
  .container {
    width: 86%;
    width: var(--container-medium);
    margin-top: 0%;
  }
  section {
    margin-top: 6rem;
  }
}

/* Phones Media Query*/
@media screen and(max-width:600px) {
  .container {
    width: 90;
    width: var(--container-small);
  }
  section > h2 {
    margin-bottom: 2rem;
  }
}

header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
  background-image: url(/portfoliio/static/media/programmer-bg.37627d03.jpg);
  background-position: center;
  background-repeat: none;
  background-size: cover;
}
.header-container {
  text-align: center;
  height: 100%;
  position: relative;
}
.info {
  margin-top: 2.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  justify-content: left;
}
.arrows-container {
  margin: 10rem;
  position: relative;
  float: left;
}
.arrows {
  width: 200px;
  height: 72px;
  position: absolute;
  margin-left: -30px;
  bottom: 20px;
}
.arrows path {
  stroke: #2994d1;
  fill: transparent;
  stroke-width: 1px;
  animation: arrow 2s infinite;
  -webkit-animation: arrow 2s infinite;
}

@keyframes arrow {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

@-webkit-keyframes arrow /*Safari and Chrome*/ {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  80% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.arrows path.a1 {
  animation-delay: -1s;
  -webkit-animation-delay: -1s; /* Safari 和 Chrome */
}

.arrows path.a2 {
  animation-delay: -0.5s;
  -webkit-animation-delay: -0.5s; /* Safari 和 Chrome */
}

.arrows path.a3 {
  animation-delay: 0s;
  -webkit-animation-delay: 0s; /* Safari 和 Chrome */
}

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
  .arrows-container {
    display: none;
  }
}
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
    background-repeat: none;
    background-size: cover;
    background-position: center;
    color: #fff;
  }
  .header-container h2 {
    color: var(--primary-color);
    text-shadow: 0 0 7px #fff, 0 0 10px #fff, 0 0 21px #fff, 0 0 42px #bc13fe,
      0 0 82px #bc13fe, 0 0 92px #bc13fe, 0 0 102px #bc13fe, 0 0 151px #bc13fe;
  }
}

.nav {
  width: 100%;
  height: 70px;
  background-color: var(--background-color);
  position: fixed;
  z-index: 2;
  top: 0%;
  overflow: hidden;
}
.nav-icon {
  margin: 0.5rem 0.1rem 0 0;
}
.nav a:hover {
  color: var(--background-color-secondary);
}
.wrapper {
  color: var(--primary-color);
  padding: 25px 5px 0px 30px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
@media screen and (max-width: 600px) {
  .wrapper {
    color: var(--primary-color);
    padding: 20px 2.5px 0px 15px;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
}

.about-container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}
.about-me {
  width: 100%;
  float: left;
  border-radius: 2rem;
  display: grid;
  place-items: center;
}
.about-img img {
  width: 100%;
  height: 100%;
  border: 1px solid black;
  overflow: hidden;
  border-radius: 2rem;
}
.about-content p {
  display: flex;
  flex: grow 1;
  max-width: 600px;
  margin: 2rem 0 2.5rem;
}
.palm {
  width: 10%;
  height: 10%;
}
.get-to-know-me {
  background-image: var(--gradient);
}
.get-to-know-me h2 {
  text-align: center;
}
.shine {
  width: 50px;
  height: 50px;
  float: right;
}
/* Tablet */
@media screen and (max-width: 1024px) {
  .about-container {
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
  }
  .about-me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
}
/* Mobile */
@media screen and (max-width: 600px) {
  .about-container {
    margin: 2rem;
    width: var(--container-small);
  }
  .about-me {
    width: 65%;
    margin: 0 auto 3rem;
  }
  .about-content p {
    margin: 1rem auto;
  }
}

.project-tittle {
  text-align: center;
}
.projects {
  background-color: var(--primary-color-secondary);
}
.portfolio-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2.5rem;
  gap: 2.5rem;
}
.portfolio-project {
  padding: 1.3rem;
  border-radius: 2rem;
  margin-bottom: 2rem;
  border: 1px solid transparent;
  cursor: pointer;
  background-color: var(--background-color-secondary);
}
.project-tittle {
  text-align: center;
  margin-bottom: 5rem auto;
  height: 5rem;
  color: var(--light);
  background-image: var(--gradient);
}
.portfoio-img {
  border-radius: 1.5rem;
  overflow: hidden;
}
@media screen and (max-width: 1024px) {
  .portfolio-container {
    margin-bottom: 20%;
    grid-template-columns: repeat(1fr, 1fr);
    grid-gap: 1.2rem;
    gap: 1.2rem;
    background-color: var(--background-color-secondary);
  }
}
@media screen and (max-width: 1024px) {
  .portfolio-container {
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 2rem;
    gap: 2rem;
  }
  .project {
    height: auto;
  }
}
@media screen and (max-width: 600px) {
  .portfolio-container {
    display: grid;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    grid-template-columns: repeat(1, 1fr);
    grid-gap: 1.5rem;
    gap: 1.5rem;
    margin-bottom: 5rem;
  }
  .portfolio-project {
    margin-bottom: 5rem;
  }
}

.container.contact-container {
  margin: 0 auto;
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  grid-gap: 12%;
  gap: 12%;
}
.contact-options {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}
.contact-option {
  color: var(--background-color-secondary);
  background-color: var(--primary-color-secondary);
  text-align: center;
  padding: 1.2rem;
  border-radius: 1.2rem;
  border: 2px solid transparent;
  transition: all 400ms ease;
}
.contact-option a {
  margin-top: 0.75rem;
  display: inline-block;
  font: 0.8rem;
}
.contact-option:hover {
  background-color: transparent;
  border-color: var(--primary-color);
}
.contact-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}
form {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}
input,
textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  border: 2px solid var(--background-color-secondary);
  resize: none;
}
button {
  margin-bottom: 2rem;
}
/* Tablet */
@media screen and (max-width: 1024px) {
  .container.contact-container {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
}
/* Mobile */
@media screen and (max-width: 600px) {
  .container.contact-container {
    margin: 2rem;
    width: var(--container-small);
  }
}

.footer {
  background-color: var(--dark);
}
.footer div {
  display: flex;
  flex-wrap: wrap;
  padding: 15px;
  height: 50px;
}
.footer h2 {
  margin: 0 auto;
  text-align: center;
  font-size: 20px;
}

